import React, { useState } from 'react';
import { Drawer, List, ListItem, ListItemText, IconButton, Box, Typography, CssBaseline, Container } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import Header from "../Test_seungwon/Test_seungwon.js";

function Background() {
  const [isOpen, setIsOpen] = useState(false);

  // Drawer   
  const toggleDrawer = (open) => (event) => {
    // Tab Shift   
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }
    setIsOpen(open);
  };

  // Drawer   
  const drawerList = () => (
    <Box
      role="presentation"
      onClick={toggleDrawer(false)}
      onKeyDown={toggleDrawer(false)}
    >
      <List>
        {['', ' '].map((text) => (
          <ListItem button key={text}>
            <ListItemText primary={text} sx={{ color: 'white', paddingLeft: 1, paddingTop: 1 }} /> {/*    */}
          </ListItem>
        ))}
      </List>
    </Box>
  );

  return (
    <Box sx={{
      width: '100%',
      minHeight: '100vh', //    100% 
      bgcolor: '#273142', //    
      color: 'white', //   
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center'
    }}>
      {/*   */}
      <Box sx={{
        width: '100%',
        height: 65,
        bgcolor: '#313D4F', //  
        color: 'white',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-start', //  
        paddingLeft: 2, //   
      }}>
        {/*    */}
        <IconButton onClick={toggleDrawer(true)} sx={{ color: 'white', marginRight: 2 }}>
          <MenuIcon />
        </IconButton>
        {/*   */}
        <Typography variant="h5">MilGam</Typography>
      </Box>
      {/* Drawer  */}
      <Drawer
        anchor='left'
        open={isOpen}
        onClose={toggleDrawer(false)}
        PaperProps={{
          sx: {
            bgcolor: '#313D4F', // Drawer   
            color: 'white', // Drawer   
            width: '12%'
          }
        }}
      >
        {drawerList()}
      </Drawer>
      {/*     */}
      <Typography variant="h6" sx={{ color: 'white', marginTop: '35px', alignSelf: 'flex-start', marginLeft: '3%' }}>
        
      </Typography>
      {/* SimpleContainer  */}
      <React.Fragment>
        <CssBaseline />
        <Container maxWidth={false} sx={{ width: '95%', bgcolor: '#313D4F', borderRadius: '12px', padding: '16px', marginTop: '10px', marginBottom: '50px' }}>
          {/*   < />   */}
          <Box 
            sx={{ 
              bgcolor: '#313D4F',
              height: '100vh', 
              borderRadius: '16px', //   
              marginTop: '50px', //  
            }} 
          />
        </Container>
      </React.Fragment>
    </Box>
  );
}

export default Background;
